import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import P2PExchangeScreen from "../screens/p2p-exchange-development";

const P2PExchangeDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "P2P Crypto Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <P2PExchangeScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="P2P Crypto Exchange Development Company | Justtry Technologies"
    headTitle="P2P Crypto Exchange Development Company | Justtry Technologies"
    description="Elevate your crypto game with Justtrys bespoke P2P crypto exchange development services. Build the next-gen P2P cryptocurrency exchange today."
    keywords="P2P crypto exchange development, cryptocurrency exchange development company, blockchain technology, decentralized finance, White label crypto exchange development"
  ></Seo>
);

export default P2PExchangeDevelopment;

export const queryData = graphql`
  query myQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
